.app-navbar-icon img {
  width: 28px;
  height: 28px;
  border-radius: 22%;
}
.app-navbar-button {
  transition: 200ms;
  opacity: 0;
}
.app-navbar-button .button {
  min-width: 72px;
}
.app-navbar.navbar-transparent-visible .app-navbar-button {
  opacity: 1;
}
.app-header {
  display: flex;
  align-items: stretch;
  margin-top: 0;
}
.app-header-icon {
  width: 118px;
  height: 118px;
  border-radius: 22%;
  margin-right: var(--f7-block-padding-horizontal);
  flex-shrink: 0;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.theme-dark .app-header-icon {
  border: 1px solid rgba(255, 255, 255, 0.1);
}
@media (min-width: 768px) {
.app-header-icon {
    width: 191px;
    height: 191px;
}
}
.app-header-content {
  display: flex;
  flex-direction: column;
  width: 100%;
  flex-shrink: 10;
  min-width: 0;
}
.app-header-title {
  font-size: 22px;
  font-weight: 600;
}
@media (min-width: 768px) {
.app-header-title {
    font-size: 27px;
}
}
.app-header-subtitle {
  font-size: 16px;
  opacity: 0.55;
}
@media (min-width: 768px) {
.app-header-subtitle {
    font-size: 20px;
}
}
.app-header-actions {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
}
.app-header-actions .button {
  min-width: 72px;
}
@media (min-width: 768px) {
.app-header-actions {
    margin-bottom: auto;
}
}
.app-header-rating {
  display: flex;
  flex-wrap: wrap;
  opacity: 0.55;
  align-items: center;
  position: relative;
}
.app-header-rating-value {
  font-size: 22px;
  font-weight: bold;
  margin-right: 4px;
}
.app-header-rating .rating-stars .f7-icons {
  font-size: 18px;
}
.app-header-rating-sub {
  position: absolute;
  left: 0;
  top: 100%;
  font-size: 12px;
  opacity: 0.75;
}
@media (min-width: 768px) {
.app-header-rating-sub {
    position: static;
    margin-left: 16px;
    font-size: 14px;
    width: 100%;
    margin-left: 0;
}
}
@media (max-width: 767px) {
.app-header {
    margin-bottom: calc(var(--f7-block-margin-vertical) + 30px + 16px);
}
.app-header-ratings {
    margin-top: 16px;
    margin-bottom: -46px;
    margin-left: calc(-118px - var(--f7-block-padding-horizontal));
}
}
.app-screenshots {
  overflow: auto;
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
  white-space: nowrap;
  font-size: 0;
  scroll-padding-left: calc(20px + var(--f7-safe-area-left));
}
.app-screenshots::-webkit-scrollbar {
  display: none;
  opacity: 0;
}
.app-screenshots img {
  width: 220px;
  border-radius: 22px;
  scroll-snap-align: start start;
  margin-right: 10px;
  background-color: #f1f1f1;
}
.theme-dark .app-screenshots img {
  background-color: #111;
}
@media (min-width: 768px) {
.app-screenshots img {
    margin-right: 24px;
}
}
.app-screenshots:after {
  content: '';
  width: 1px;
  height: 1px;
  flex-shrink: 0;
}
.app-description {
  font-size: 15px;
}
.app-description-content {
  position: relative;
  padding-top: 16px;
}
.app-description-content:before {
  content: '';
  position: absolute;
  background-color: var(--f7-block-strong-border-color);
  display: block;
  z-index: 15;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  height: 1px;
  width: 100%;
  transform-origin: 50% 0%;
  transform: scaleY(calc(1 / var(--f7-device-pixel-ratio)));
}
.app-description .link {
  position: absolute;
  bottom: 0;
  right: 0;
}
.app-description .link.active-state {
  opacity: 1;
}
.app-description .link:before {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 200%;
  background-image: linear-gradient(to left, #fff 50%, rgba(255, 255, 255, 0));
  z-index: -1;
}
.theme-dark .app-description .link:before {
  background-image: linear-gradient(to left, #000 50%, rgba(0, 0, 0, 0));
}
.app-description-text {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
}
.app-description-full .app-description-text {
  display: block;
  animation: app-description-fade-in 600ms;
}
.app-description-full .link {
  display: none;
}
@keyframes app-description-fade-in {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.app-ratings {
  display: flex;
  margin-bottom: 0;
}
.app-ratings-number {
  line-height: 1;
  text-align: center;
  font-weight: bold;
  flex-shrink: 0;
}
.app-ratings-number b,
.app-ratings-number span {
  display: block;
}
.app-ratings-number b {
  font-size: 60px;
  letter-spacing: -0.05em;
  font-weight: 800;
}
.app-ratings-number span {
  font-size: 15px;
  opacity: 0.55;
}
.app-ratings-votes {
  width: 100%;
  flex-shrink: 10;
  line-height: 10px;
  margin-top: 6px;
}
.app-ratings-votes i {
  font-size: 8px;
  opacity: 0.55;
}
.app-ratings-votes-row {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.app-ratings-votes-progress {
  width: 70%;
  height: 2px;
  background: rgba(0, 0, 0, 0.17);
  border-radius: 2px;
  margin-left: 10px;
}
.app-ratings-votes-progress span {
  background: rgba(0, 0, 0, 0.55);
  display: block;
  height: 2px;
  border-radius: 2px;
}
.theme-dark .app-ratings-votes-progress {
  background: rgba(255, 255, 255, 0.17);
}
.theme-dark .app-ratings-votes-progress span {
  background: rgba(255, 255, 255, 0.55);
}
.app-ratings-votes-total {
  text-align: right;
  opacity: 0.55;
  margin-top: 4px;
}
.app-reviews {
  margin-top: 16px;
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
  display: flex;
  overflow: auto;
}
.app-reviews::-webkit-scrollbar {
  display: none;
  opacity: 0;
}
@media (min-width: 768px) {
.app-reviews {
    scroll-padding-left: calc(20px + var(--f7-safe-area-left));
}
}
.app-reviews:after {
  content: '';
  flex-shrink: 0;
  width: 1px;
  height: 1px;
}
.app-reviews .app-review {
  border-radius: 10px;
  background: var(--f7-block-strong-bg-color);
  padding: 16px;
  scroll-snap-align: center center;
  width: 100%;
  max-width: 350px;
  margin-right: 10px;
  flex-shrink: 0;
  box-sizing: border-box;
}
html:not(.theme-dark) .app-reviews .app-review {
  background-color: #F2F2F6;
}
@media (min-width: 768px) {
.app-reviews .app-review {
    margin-right: 24px;
    scroll-snap-align: start start;
}
}
.app-reviews .app-review-header {
  display: flex;
  justify-content: space-between;
}
.app-reviews .app-review-header span:last-child {
  opacity: 0.55;
}
.app-reviews .app-review-header .rating-stars .f7-icons {
  font-size: 12px;
  color: var(--f7-color-orange);
}
.app-reviews .app-review-text {
  margin-top: 16px;
}
.app-information-list {
  --f7-list-bg-color: transparent;
  --f7-list-item-title-text-color: rgba(0, 0, 0, 0.55);
  --f7-list-item-after-text-color: #000;
}
.theme-dark .app-information-list {
  --f7-list-item-after-text-color: #fff;
  --f7-list-item-title-text-color: rgba(255, 255, 255, 0.55);
}
.app-information-list .item-link {
  --f7-list-item-title-text-color: var(--f7-theme-color);
  --f7-list-item-after-text-color: var(--f7-theme-color);
}
